import React, { useState, useEffect, useRef } from "react";
import audio from "./gong.mp3"

function App() {
  
const [sessLenght, setSessLenght] = useState(25*60000);
const [breakLenght, setBreakLenght] = useState(5*60000);
const [session, setSession] = useState(true)
const [timerRunning, setTimerRunning] = useState(false);
const [sessionTimeLeft, setSessionTimeLeft] = useState(sessLenght);
const [breakTimeLeft, setBreakTimeLeft] = useState(breakLenght);
const [blink, setBlink] = useState(false)

const audioRef = useRef()

useEffect(()=>{
  if (!blink) {
    session? document.title = "Session " + timeFormat(sessionTimeLeft)
   : document.title = "Break " + timeFormat(breakTimeLeft);
  } else{
    document.title = "";
  }
});

useInterval(()=>{
  if(sessionTimeLeft<10000 || breakTimeLeft<10000){
    if (!blink) {
      setBlink(true)
    } else {
      setBlink(false)
    }
  } else{
    setBlink(false)
  };
  if (session){ 
  setSessionTimeLeft(sessionTimeLeft-1000)
  }else{ 
    setBreakTimeLeft(breakTimeLeft-1000)
  }
}, timerRunning ? 1000 : null)


useInterval(()=>{
  if(sessionTimeLeft === 0){
    setSession(false)
    setSessionTimeLeft(sessLenght)
    audioRef.current.play();
  }else if(breakTimeLeft === 0){
    setSession(true)
    setBreakTimeLeft(breakLenght)
    audioRef.current.play();
  }
}, timerRunning ? 1000 : null)




//-----------------------------------------------------------------------
// https://overreacted.io/making-setinterval-declarative-with-react-hooks/
// custom hook useInterval by Dan Ambramov

function useInterval(callback, delay) {
  const savedCallback = useRef();

  // Remember the latest callback.
  useEffect(() => {
    savedCallback.current = callback;
  }, [callback]);

  // Set up the interval.
  useEffect(() => {
    function tick() {
      savedCallback.current();
    }
    if (delay !== null) {
      let id = setInterval(tick, delay);
      return () => clearInterval(id);
    }
  }, [delay]);
}

//--------------------------------------------------------------------------
const startPausHandle =()=> {
  setTimerRunning(timerRunning? false : true)
}

const resetHandle = () =>{
  setTimerRunning(false);
  setSession(true)
  setSessLenght (25*60000);
  setBreakLenght(5*60000);
  setSessionTimeLeft(25*60000);
  setBreakTimeLeft(5*60000);
  audioRef.current.pause();
  audioRef.current.currentTime = 0;
};

const incrementHandle = (e) =>{

  if(e.target.id === "session-increment" && sessLenght<3600000){ 
    setSessLenght(sessLenght+60000)
    setSessionTimeLeft(sessLenght+60000);
  }else if(e.target.id === "break-increment" && breakLenght<3600000){
    setBreakLenght(breakLenght+60000)
    setBreakTimeLeft(breakTimeLeft+60000)
  }
};

const decrementHandle = (e) =>{
  if(e.target.id === "session-decrement" && sessLenght>60000){ 
    setSessLenght(sessLenght-60000);
    setSessionTimeLeft(sessLenght-60000);
  }else if(e.target.id === "break-decrement" && breakLenght>60000){
    setBreakLenght(breakLenght-60000);
    setBreakTimeLeft(breakLenght-60000);
  }
};

const timeFormat =(t) =>{
  let m = Math.floor(t/60000);
  let s = Math.floor((t%60000)/1000)
  return m.toString().padStart(2,0)+":"+s.toString().padStart(2,0);
};

const mouseDownHandle =(e)=>{
  document.getElementById(e.target.id).classList.add("clickActive")
}

const mouseUpHandle =(e)=>{
  document.getElementById(e.target.id).classList.remove("clickActive")
}

  return (
    
    <div className="App">
      <audio id="beep" ref={audioRef}>
        <source src={audio} type="audio/mp3"/>
      </audio>
      <div className="container"> 
      <div id="session-container" className="sub-container">
        <div id="session-label" className="label">SESSION</div>
        <div className="sub-sub-container">
          <div id="session-length">{sessLenght/60000}</div>
          <div className="min-div"> min</div>
        </div>
          <div className="controls">
          <i className="fas fa-plus"   id="session-increment" onClick={incrementHandle} onMouseDown={mouseDownHandle} onMouseUp={mouseUpHandle}></i>
          <i className="fas fa-minus" id="session-decrement" onClick={decrementHandle} onMouseDown={mouseDownHandle} onMouseUp={mouseUpHandle}></i>
        </div>
      </div>
      <div id="break-container" className="sub-container">
        <div id="break-label" className="label">BREAK</div>
        <div className="sub-sub-container">
          <div id="break-length">{breakLenght/60000}</div>
          <div className="min-div"> min</div>
        </div>
        <div className="controls">
          <i className="fas fa-plus" id="break-increment" onClick={incrementHandle} onMouseDown={mouseDownHandle} onMouseUp={mouseUpHandle}></i>
          <i className="fas fa-minus" id="break-decrement" onClick={decrementHandle} onMouseDown={mouseDownHandle} onMouseUp={mouseUpHandle}></i>
        </div>
      </div>
      <div id="time-display-container" className="sub-container">
        <div id="timer-label" className={blink? "blink" : null}>{session? "SESSION" : "BRAKE"}</div>
        <time id="time-left">
        {session? timeFormat(sessionTimeLeft) : timeFormat(breakTimeLeft)}
        </time>
        <div className="controls">
          <i id="start_stop" onClick={startPausHandle} 
              className={timerRunning? "fas fa-pause" : "fas fa-play"} onMouseDown={mouseDownHandle} onMouseUp={mouseUpHandle}></i> 
          <i className="fas fa-sync" id="reset" onClick={resetHandle} onMouseDown={mouseDownHandle} onMouseUp={mouseUpHandle}></i>
        </div> 
      </div>
      </div>
    </div>
  );
}

export default App;

